@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400&display=swap);

:root{
    --blue:#3c6382;
    --dark-blue:#0a3d62;
}

*{
    font-family: 'Poppins', sans-serif;
    margin:0; padding:0;
    box-sizing: border-box;
    text-decoration: none;
    outline: none;
    border:none;
    text-transform: capitalize;
    transition: all .3s cubic-bezier(.38,1.15,.7,1.12);
}

*::selection{
    background:#0a3d62;
    background:var(--dark-blue);
    color:#fff;
}

html{
    font-size: 62.5%;
    overflow-x: hidden;
}

section{
    min-height: 100vh;
    padding:1rem 9%;
    padding-top: 5rem;
}

.heading{
    text-align: center;
    color:#3c6382;
    color:var(--blue);
    font-size: 3.5rem;
    text-transform: uppercase;
    font-weight: normal;
    padding:1rem;
}

header{
    width:100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    top:0; left:0;
    z-index: 1000;
    padding: 1rem;
    background: aliceblue;
}

header .logo{
    font-size: 2.5rem;
    color:#0a3d62;
    color:var(--dark-blue);
}

header .logo span{
    color:#3c6382;
    color:var(--blue);
}

header .navbar a{
    font-size: 1.7rem;
    margin-left: .7rem;
    padding:.5rem 2rem;
    border-radius: .5rem;
    color:#0a3d62;
    color:var(--dark-blue);
    /*border: 1px solid #3c6382;*/
}

header .navbar a.active,
header .navbar a:hover{
    background: #3c6382;
    background: var(--blue);
    color:#fff;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
}

header.sticky{
    background:#fff;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
}

#menu{
    font-size: 3rem;
    color:#3c6382;
    color:var(--blue);
    cursor: pointer;
    display: none;
}

.home{
    background:url(/covid19-reactapp/static/media/home-bg.f7053293.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}

.home .content{
    text-align: center;
    padding-top: 3rem;
}

.home .content h1{
    font-size: 5rem;
    color:#0a3d62;
    color:var(--dark-blue);
}

.home .content h3{
    font-size: 4rem;
    color:#3c6382;
    color:var(--blue);
    font-weight: normal;
}

.home .content .btn{
    display: inline-block;
    padding:.7rem 3rem;
    color:#fff;
    background:#3c6382;
    background:var(--blue);
    border-radius: .5rem;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
    margin-top: 1rem;
    font-size: 1.7rem;
}

.home .content .btn:hover{
    background:#0a3d62;
    background:var(--dark-blue);
}

.prevent{
    background:url(/covid19-reactapp/static/media/background-img.6b8604a0.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}

.prevent .box-container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.prevent .box-container .box{
    flex:1 1 30rem;
    border-radius: .5rem;
    margin:1rem;
    padding:1rem;
    text-align: center;
}

.prevent .box-container .box img{
    height:12rem;
    padding:1rem;
}

.prevent .box-container .box h3{
    color:#3c6382;
    color:var(--blue);
    font-size: 2rem;
}

.prevent .box-container .box p{
    color:#0a3d62;
    color:var(--dark-blue);
    font-size: 1.4rem;
    padding:1rem;
}

.prevent .box-container .box:hover{
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
}


.creator{
    background:url(/covid19-reactapp/static/media/background-img.6b8604a0.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}

.creator .box-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.creator .box-container .box{
    border-radius: 1rem;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
    padding:1rem;
    margin:1.5rem;
    text-align: center;
    background: aliceblue;
    border:.1rem solid rgba(0,0,0,.1);
    width:26rem;
}

.creator .box-container .box img{
    margin:1rem;
    border-radius: 50%;
    height:20rem;
    width:20rem;
    object-fit: cover;
    background:#3c6382;
    background:var(--blue);
}

.creator .box-container .box h3{
    font-size: 2rem;
    color:#3c6382;
    color:var(--blue);
}

.creator .box-container .box span{
    font-size: 1.5rem;
    color:#0a3d62;
    color:var(--dark-blue);
}

.creator .box-container .box .share{
    border-top: .1rem solid rgba(0,0,0,.1);
    padding:1rem;
    margin-top: 1rem;
}

.creator .box-container .box .share a{
    border-radius: 50%;
    height: 4.5rem;
    width:4.5rem;
    line-height: 4.5rem;
    font-size: 2rem;
    margin: .4rem;
    background: aliceblue;
    color:#3c6382;
    color:var(--blue);
}

.creator .box-container .box .share a:hover{
    background:#3c6382;
    background:var(--blue);
    color:#fff;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}

a {
    text-decoration: none;
}

.credit{
    font-size: 2rem;
    font-weight: normal;
    text-align: center;
    border-top: .1rem solid rgba(0,0,0,.1);
    padding:1.5rem 1rem;
    background: aliceblue;
    color:#0a3d62;
    color:var(--dark-blue);
}

.credit a{
    color:#3c6382;
    color:var(--blue);
}



.container-fluid {
    font-family: 'Poppins', sans-serif;
    width: 100%;
    margin: auto;
}

.livestate {
    background:url(/covid19-reactapp/static/media/background-img.6b8604a0.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}

.table-responsive {
    border-radius: 10px;
    /*max-height: 500px;*/
    border: 1px solid lightgrey;
}

tr:hover {
    background-color: #fff;
}

td,th {
    position: relative;
}

.table td, .table th {
    padding: 1rem;
    vertical-align: top;
    border-top: none;
    font-size: 14px;
}

.table-dark {
    font-weight: bold;
    text-transform: uppercase;
}

